import type { FC, ReactElement } from 'react';
import { Fragment } from 'react';
import type { ICommonProps } from '@type';
import './index.css';

export interface IActionBar extends ICommonProps {
    /**
     * React Element arrays to display actions on the left section
     */
    leftActions?: ReactElement[]
    /**
     * React Element arrays to display actions int the center section
     */
    centerActions?: ReactElement[]
    /**
     * React Element arrays to display actions on the right section
     */
    rightActions?: ReactElement[]
}

export const ActionBar: FC<IActionBar> = ({ leftActions = [], centerActions = [], rightActions = [], className, style }) => {
    return (
        <div className={`action-bar ${className}`} style={style}>
            <div className='left-action-group'>
                {leftActions.map((item, index) => <Fragment key={index}>{item}</Fragment>)}
            </div>
            <div className='action-center-group'>
                {centerActions.map((item, index) => <Fragment key={index}>{item}</Fragment>)}
            </div>
            <div className='right-action-group'>
                {rightActions.map((item, index) => <Fragment key={index}>{item}</Fragment>)}
            </div>
        </div>
    );
};
