import type { FC } from 'react';
import React, { useRef, useState } from 'react';
import { Box, Button, Dialog, DialogContent, IconButton } from '@mui/material';
import { CallEnd, Close } from '@mui/icons-material';
import { MeetingReduceType, useGlobalContext, useMeetingContext } from '@provider';
import useTranslation from '@hooks/useTranslation';
import type { ILabelActionProps } from '@type';
import './index.css';

export const LeaveAction: FC<ILabelActionProps> = ({ isShowLabel = false, onError, className, style }) => {
    const actionButtonRef = useRef<HTMLDivElement | null>(null);

    const { rcvEngine } = useGlobalContext();
    const meetingController = rcvEngine?.getMeetingController();

    const { state: meetingState, dispatch } = useMeetingContext();
    const { t } = useTranslation();

    const [isShowLeaveOption, setIsShowLeaveOption] = useState(false);

    const leaveMeetingHandler = async () => {
        if (
            meetingState.localParticipant &&
            (meetingState.localParticipant.isModerator || meetingState.localParticipant.isHost) &&
            meetingState.remoteParticipantList.length > 0
        ) {
            setIsShowLeaveOption(true);
        } else {
            await leaveMeeting();
        }
    };

    const leaveMeeting = async () => {
        try {
            await meetingController?.leaveMeeting();
            dispatch({
                type: MeetingReduceType.RESET,
                payload: {},
            });
        } catch (e) {
            onError && onError(e);
        }
    };

    const endMeeting = async () => {
        try {
            await meetingController?.endMeeting();
            dispatch({
                type: MeetingReduceType.RESET,
                payload: {},
            });
        } catch (e) {
            onError && onError(e);
        }
    };

    return (
        <>
            <div className={`action-button action-leave ${className || ''}`} ref={actionButtonRef} style={style} onClick={leaveMeetingHandler}>
                <CallEnd className='action-button-icon' sx={{ color: '#ea4335' }}></CallEnd>
                {isShowLabel && <div className='action-button-label'>{t.leave_label}</div>}
            </div>
            <Dialog
                open={isShowLeaveOption}
                onClose={() => setIsShowLeaveOption(false)}>
                <DialogContent className={'leave-meeting-dialog'} dividers>
                    <div className={'leave-meeting-dialog-header'}>
                        <div className={'leave-meeting-dialog-title RCV-title-font'}>{t.leave_dialog_title}</div>
                        <div className={'leave-meeting-dialog-close-action'}>
                            <IconButton
                                aria-label='close'
                                color='inherit'
                                edge='start'
                                onClick={() => setIsShowLeaveOption(false)}>
                                <Close />
                            </IconButton>
                        </div>
                    </div>
                    <Box className='leave-meeting-dialog-action'>
                        <div className='leave-meeting-dialog-action-item'>
                            <Button fullWidth variant='contained' onClick={leaveMeeting}>{t.leave_dialog_btn_leave}</Button>
                        </div>
                        <div className='leave-meeting-dialog-action-item'>
                            <Button fullWidth variant='text' onClick={endMeeting}>{t.leave_dialog_btn_end}</Button>
                        </div>
                    </Box>
                </DialogContent>
            </Dialog>
        </>
    );
};

