import type { FC } from 'react';
import React from 'react';
import { Chat as ChatIcon } from '@mui/icons-material';

import { MeetingReduceType, useMeetingContext } from '@provider';
import { ActiveFeatureModal } from '@provider/meeting/meeting-state';
import { MeetingModal } from '@components/meeting-modal';
import useTranslation from '@hooks/useTranslation';
import { ChatPanel } from './chat-panel';
import type { ILabelActionProps } from '@type';

export const ChatAction: FC<ILabelActionProps> = ({ isShowLabel = false, className, style,onError }) => {
    const { state: meetingState, dispatch } = useMeetingContext();
    const { t } = useTranslation();

    const showChatModal = () => {
        dispatch({
            type: MeetingReduceType.ACTIVE_FEATURE_MODAL,
            payload: { activeFeatureModal: ActiveFeatureModal.Chat },
        });
    };

    return (
        <>
            <div className={`action-button ${className || ''}`} style={style} onClick={showChatModal}>
                <ChatIcon className='action-button-icon'></ChatIcon>
                {isShowLabel && <div className='action-button-label'> {t.chat_label}</div>}
            </div>
            {meetingState.activeFeatureModal === ActiveFeatureModal.Chat ? (
                <MeetingModal title={t.chat_dialog_title}>
                    <ChatPanel onError={onError}/>
                </MeetingModal>
            ) : null}
        </>
    );
};
