import type { FC } from 'react';
import React, { useEffect, useRef, useState } from 'react';
import { Popover } from '@mui/material';
import { Videocam, VideocamOff, KeyboardArrowUp } from '@mui/icons-material';
import { VideoEvent } from '@ringcentral/video-sdk';
import { useGlobalContext, useMeetingContext } from '@provider';
import useVideoDevices from '@hooks/useVideoDevices';
import useTranslation from '@hooks/useTranslation';
import DeviceListPanel, { ICON_STYLE } from '@components/device-list-panel';
import ConfirmPopover from '@components/confirm-popover';
import type { ILabelActionProps } from '@type';
import '../action.css';

export interface IVideoAction extends ILabelActionProps {
    /**
     * If is true, it shows cameras recording devices panel in popover by click arrow icon.
     */
    isDeviceListShown?: boolean
    /**
     * Callback fired when toggle video mute.
     */
    localMuteCallback?: (mute: boolean) => void
}

export const VideoAction: FC<IVideoAction> = ({ isShowLabel = false, isDeviceListShown = true, localMuteCallback = (mute: boolean) => undefined, onError, className, style }) => {
    const [isShowVideoUnmuteDemandPop, setIsShowVideoUnmuteDemandPop] = useState(false);
    const [isShowVideoDeviceList, setIsShowVideoDeviceList] = useState(false);

    const { rcvEngine } = useGlobalContext();
    const meetingController = rcvEngine?.getMeetingController();
    const { state: meetingState } = useMeetingContext();
    const [videoDeviceList, videoActiveDevice] = useVideoDevices();
    const { t } = useTranslation();
    const actionButtonRef = useRef(null);

    useEffect(() => {
        initListener();
    }, []);


    const initListener = () => {
        const videoController = meetingController?.getVideoController();

        const videoMuteDemandListener = videoController?.on(VideoEvent.VIDEO_UNMUTE_DEMAND, () => {
            setIsShowVideoUnmuteDemandPop(true);
        });
        return () => {
            videoMuteDemandListener?.();
        };
    };

    const unmuteLocalVideoStream = async() => {
        const constraints = videoActiveDevice?{
            deviceId: videoActiveDevice
        }:undefined;
        await meetingController?.getVideoController()?.unmuteLocalVideoStream(constraints);
    };

    const toggleMuteVideo = async () => {
        const isMute = meetingState.isVideoMuted;
        try {
            if (isMute) {
                await unmuteLocalVideoStream();
            } else {
                await meetingController?.getVideoController()?.muteLocalVideoStream();
            }
            localMuteCallback(!isMute);
        } catch (e) {
            onError && onError(e);
        }
    };

    const acceptUnMuteVideoDemand = async () => {
        try {
            await unmuteLocalVideoStream();
            setIsShowVideoUnmuteDemandPop(false);
        } catch (e) {
            onError && onError(e);
        }
    };

    const handleChangeVideoDevice = async (deviceId: string) => {
        try {
            await rcvEngine?.getVideoDeviceManager().setVideoDevice(deviceId);
            if(!meetingState.isVideoMuted){
                await rcvEngine?.getMeetingController()
                    .getVideoController()
                    .unmuteLocalVideoStream({ deviceId });
            }
        } catch (e) {
            onError && onError(e);
        }
    };

    return (
        <>
            <div className='action-button' ref={actionButtonRef} onClick={toggleMuteVideo}>
                {meetingState.isVideoMuted ? <VideocamOff className='action-button-icon' /> : <Videocam className='action-button-icon' />}
                {isShowLabel &&
                    <div className='action-button-label'>
                        {meetingState.isVideoMuted ? t.video_label_mute : t.video_label_unmute}
                    </div>}
                {isDeviceListShown &&
                    <div
                        className={`action-button-more ${isShowVideoDeviceList ? 'checked' : ''}`}
                        onClick={e => {
                            e.stopPropagation();
                            setIsShowVideoDeviceList(!isShowVideoDeviceList);
                        }}>
                        <KeyboardArrowUp sx={{ fontSize: '14px' }}></KeyboardArrowUp>
                    </div>
                }
            </div>
            <Popover
                anchorEl={actionButtonRef.current}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
                open={isShowVideoDeviceList}
                transformOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                onClose={() => setIsShowVideoDeviceList(false)}>
                <div className='meeting-popover center-bottom'>
                    <DeviceListPanel
                        defaultDevice={videoActiveDevice}
                        deviceList={videoDeviceList}
                        title={t.video_device_list_camera}
                        titleIcon={<Videocam sx={ICON_STYLE} />}
                        onDeviceChange={handleChangeVideoDevice}
                    />
                </div>
            </Popover>
            <Popover
                anchorEl={actionButtonRef.current}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
                open={Boolean(actionButtonRef.current) && isShowVideoUnmuteDemandPop}
                transformOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                onClose={() => setIsShowVideoUnmuteDemandPop(false)}>
                <ConfirmPopover
                    desc={t.video_remote_unmute_desc}
                    title={t.video_remote_unmute_title}
                    onDeny={() => setIsShowVideoUnmuteDemandPop(false)}
                    onOk={acceptUnMuteVideoDemand}
                />
            </Popover>

        </>
    );
};
