import type { FC } from 'react';
import React, { useEffect, useRef, useState } from 'react';

import { Popover } from '@mui/material';
import { MicOff, Mic, KeyboardArrowUp } from '@mui/icons-material';
import { AudioEvent } from '@ringcentral/video-sdk';
import { useGlobalContext, useMeetingContext } from '@provider';
import useAudioDevices from '@hooks/useAudioDevices';
import useTranslation from '@hooks/useTranslation';
import DeviceListPanel, { ICON_STYLE } from '@components/device-list-panel';
import ConfirmPopover from '@components/confirm-popover';
import type { ILabelActionProps } from '@type';

import '../action.css';

export interface IAudioActionProps extends ILabelActionProps {
    /**
     * If is true, it shows audio recording devices panel in popover by click arrow icon.
     */
    isDeviceListShown?: boolean
    /**
     * Callback fired when toggle audio mute.
     */
    localMuteCallback?: (mute: boolean) => void
}

export const AudioAction: FC<IAudioActionProps> = ({ isShowLabel = false, isDeviceListShown = true, localMuteCallback = (mute: boolean) => undefined, onError, style, className }) => {
    const [isShowAudioUnmuteDemandPop, setIsShowAudioUnmuteDemandPop] = useState(false);
    const [isShowAudioDeviceList, setIsShowAudioDeviceList] = useState(false);

    const { rcvEngine } = useGlobalContext();
    const meetingController = rcvEngine?.getMeetingController();
    const { state: meetingState } = useMeetingContext();
    const [recordingDeviceList, recordingActiveDevice] = useAudioDevices(onError);
    const { t } = useTranslation();
    const actionButtonRef = useRef(null);

    useEffect(() => {
        initListener();
    }, []);

    const initListener = () => {
        const audioController = meetingController?.getAudioController();

        const audioMuteDemandListener = audioController?.on(AudioEvent.AUDIO_UNMUTE_DEMAND, () => {
            setIsShowAudioUnmuteDemandPop(true);
        });
        return () => {
            audioMuteDemandListener?.();
        };
    };

    const toggleMuteAudio = async () => {
        const isMute = meetingState.isAudioMuted;
        try {
            if (isMute) {
                await meetingController?.getAudioController()?.unmuteLocalAudioStream();
            } else {
                await meetingController?.getAudioController()?.muteLocalAudioStream();
            }
            localMuteCallback(!isMute);
        } catch (e) {
            onError && onError(e);
        }
    };

    const acceptUnMuteAudioDemand = async () => {
        try {
            await meetingController?.getAudioController()?.unmuteLocalAudioStream();
            setIsShowAudioUnmuteDemandPop(false);
        } catch (e) {
            onError && onError(e);
        }
    };

    const onAudioDeviceChange = async (deviceId: string) => {
        try {
            await rcvEngine?.getAudioDeviceManager().setRecordingDevice(deviceId);
            await rcvEngine?.getMeetingController().getAudioController().enableAudio({ deviceId });
        } catch (e) {
            onError && onError(e);
        }
    };

    return (
        <>
            <div className={`action-button ${className || ''}`} ref={actionButtonRef} style={style} onClick={toggleMuteAudio}>
                {meetingState.isAudioMuted ? <MicOff className='action-button-icon' /> : <Mic className='action-button-icon' />}
                {isShowLabel &&
                    <div className='action-button-label'>
                        {meetingState.isAudioMuted ? t.audio_label_mute : t.audio_label_unmute}
                    </div>}
                {isDeviceListShown &&
                    <div
                        className={`action-button-more ${isShowAudioDeviceList ? 'checked' : ''}`}
                        onClick={e => {
                            e.stopPropagation();
                            setIsShowAudioDeviceList(!isShowAudioDeviceList);
                        }}>
                        <KeyboardArrowUp sx={{ fontSize: '14px' }}></KeyboardArrowUp>
                    </div>
                }
            </div>
            <Popover
                anchorEl={actionButtonRef.current}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
                open={isShowAudioDeviceList}
                transformOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                onClose={() => setIsShowAudioDeviceList(false)}>
                <div className='meeting-popover center-bottom'>
                    <DeviceListPanel
                        defaultDevice={recordingActiveDevice}
                        deviceList={recordingDeviceList}
                        title={t.audio_device_list_microphone}
                        titleIcon={<Mic sx={ICON_STYLE} />}
                        onDeviceChange={onAudioDeviceChange}
                    />
                </div>
            </Popover>
            <Popover
                anchorEl={actionButtonRef.current}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
                open={Boolean(actionButtonRef.current) && isShowAudioUnmuteDemandPop}
                transformOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                onClose={() => setIsShowAudioUnmuteDemandPop(false)}>
                <ConfirmPopover
                    desc={t.audio_remote_unmute_desc}
                    title={t.audio_remote_unmute_title}
                    onDeny={() => setIsShowAudioUnmuteDemandPop(false)}
                    onOk={acceptUnMuteAudioDemand}
                />
            </Popover>
        </>
    );
};
